<template>
    <div class="payment">
        <header>
            <a href="javascript:history.back(-1)" class="h_a_1">
                <i class="el-icon-arrow-left"></i>
            </a>
            <span>在线支付</span>
        </header>
        <section class="s1">
            <p>支付剩余时间</p>
            <p>00 : {{minute}} : {{second}}</p>
        </section>
        <section class="s2">选择支付方式</section>
        <section class="s3">
            <div class="s31">
                <div>
                    <img src="http://img.zcool.cn/community/01db1c5ad0dab9a8012138678d8db9.png@2o.png" alt="" class="img">
                    <span>支付宝</span>
                </div>
                <i class="el-icon-success" @click="sex=1" :style="{'color':sex==1 ? '#4cd964' : '#ccc'}"></i>
            </div>
            <div class="s32">
                <div>
                    <img src="http://pic38.photophoto.cn/20160325/0007019842511399_b.jpg" alt="" class="img">
                    <span>微信</span>
                </div>
                <i class="el-icon-success" @click="sex=2" :style="{'color':sex==2 ? '#4cd964' : '#ccc'}"></i>
            </div>
        </section>
        <div class="s4" @click="isShow3 = !isShow3">确认支付</div>
        <section class="s5" v-if="isShow1">
            <div class="s51">
                <span></span>
                <span></span>
            </div>
            <p>暂不开放支付功能</p>
            <div class="s52" @click="isShow1 = !isShow1">确认</div>
        </section>
        <section class="s5" v-if="isShow2">
            <div class="s51">
                <span></span>
                <span></span>
            </div>
            <p>支付超时</p>
            <div class="s52" @click="isShow2 = !isShow2">确认</div>
        </section>
        <section class="s5" v-if="isShow3">
            <div class="s51">
                <span></span>
                <span></span>
            </div>
            <p>当前环境无法支付，请打开官方APP进行付款</p>
            <div class="s52" @click="toOrder">确认</div>
        </section>
    </div>
</template>

<script>
    export default {
        name: "Payment",
        data(){
            return{
                auth_time:900,
                minute:15,
                second:"00",
                isShow1:true,
                isShow2:false,
                isShow3:false,
                sex:1,
            }
        },
        created(){
            let auth_timetimer = setInterval(() => {
                this.auth_time -= 1;
                if (this.auth_time <= 0) {
                    this.isShow2 = true;
                    clearInterval(auth_timetimer);
                }
                this.minute = Math.floor(this.auth_time / 60);
                this.second = this.auth_time % 60;
                if (this.second < 10){
                    this.second = "0"+this.second;
                }
                if (this.minute < 10){
                    this.minute = "0"+this.minute;
                }
            }, 1000);
        },
        methods:{
            toOrder(){
                this.isShow3 = !this.isShow3;
                this.$router.push("/order");
            }
        }
    }
</script>

<style scoped>
    .payment{
        width: 100%;
        padding-top: 1.95rem;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    .s1{
        background-color: #fff;
        padding: .7rem;
        text-align: center;
    }
    .s1>p:nth-child(1){
        font-size: .6rem;
        color: #666;
        margin-top: 1rem;
    }
    .s1>p:nth-child(2){
        font-size: 1.5rem;
        color: #333;
        margin: .8rem 0 1rem;
    }
    .s2{
        background-color: #f1f1f1;
        padding: 0 .7rem;
        font-size: .7rem;
        color: #666;
        line-height: 1.8rem;
    }
    .s3{
        background-color: #fff;
    }
    .s31,.s32{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: .4rem .7rem;
        line-height: 2.6rem;
        border-bottom: .025rem solid #f5f5f5;
    }
    .s31>div,.s32>div{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .img{
        width: 1.8rem;
        height: 1.8rem;
        margin-right: .2rem;
    }
    .s3 span{
        font-size: .7rem;
        color: #666;
    }
    .s4{
        background-color: #4cd964;
        font-size: .7rem;
        color: #fff;
        text-align: center;
        margin: 0.5rem .7rem;
        line-height: 1.8rem;
        border-radius: .2rem;
        font-weight: 700;
    }
    .s5{
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -6rem;
        margin-left: -6rem;
        width: 12rem;
        animation: tipMove .4s;
        background-color: #fff;
        padding-top: .6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border: 1px;
        border-radius: .25rem;
    }
    .s51{
        width: 3rem;
        height: 3rem;
        border: .15rem solid #f8cb86;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .s51>span:nth-child(1){
        width: .12rem;
        height: 1.5rem;
        background-color: #f8cb86;
    }
    .s51>span:nth-child(2){
        width: .2rem;
        height: .2rem;
        border: 1px;
        border-radius: 50%;
        margin-top: .2rem;
        background-color: #f8cb86;
    }
    .s5>p{
        font-size: .7rem;
        color: #333;
        line-height: .9rem;
        text-align: center;
        margin-top: .8rem;
        padding: 0 .4rem;
    }
    .s52{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        margin-top: .8rem;
        background-color: #4cd964;
        width: 100%;
        text-align: center;
        line-height: 1.8rem;
        border: 1px;
        border-bottom-left-radius: .25rem;
        border-bottom-right-radius: .25rem;
    }
</style>